import priceSelect from '../price-select/sl-filter.vue';
import Messages from '../../util/messages.js';
import Constant from '../../util/constant.js'
export default {
	data() {
		return {
			defaultImg: Constant.defaultImg, //默认图片
			flag:true, //修改页面背景
			themeColor:'rgba(255,136,0,0.1)',
			list: [], //结果列表
			// 请求参数
			params:{					
				decorateType:'',
				houseArea:'',
				customerHouseType:'',
				style:''
			},
			// 下拉框列表
			menuList: [{
					'title': '',
					'detailTitle': '装修类型',
					'key': 'decorateType',
					'isMutiple': false,
					'detailList': []
				},
				{
					'title': '',
					'key': 'area',
					'isSort':true,
					'detailTitle': '房屋面积',
					'detailList': []
				},
				{
					'title': '',
					'key': 'layout',
					'isMutiple': false,
					'detailTitle': '装修户型',
					'detailList': []
				},
				{
					'title': '',
					'key': 'style',
					'isMutiple': false,
					'detailTitle': '装修风格',
					'detailList': []
				}
			],
			layoutList:[] ,//户型集合
			decTypeList:[],
			decIndex:''
		};
	},
	components:{
		priceSelect
	},
	methods:{
		// 下拉框结果
		result(val) {
			console.log(val)
			if(val == undefined){
				
			}else{
				let priceList = uni.getStorageSync('priceList');
				priceList[0].priceDectype = val.decorateType;
				let decIndex = this.decTypeList.findIndex(item=>{
					return item.title == val.decorateType
				})
				this.decIndex = decIndex
				priceList[2].priceLayout = val.layout;
				priceList[3].priceManner = val.style;
				this.params.style = val.style;
				this.params.decorateType = val.decorateType;
				if(val.area.indexOf('m²') == -1){
					let priceList = uni.getStorageSync('priceList');
					this.params.houseArea = priceList[1].priceArea;
				}else{
					this.params.houseArea = val.area.slice(0,val.area.indexOf('m²'));
					priceList[1].priceArea =val.area.slice(0,val.area.indexOf('m²'));
					// this.menuList[2].defaultSelectedIndex = 0;
					// this.$refs.slFilter.resetMenuList(this.menuList);
				}
				this.params.customerHouseType = val.layout;
				uni.setStorageSync('priceList',priceList);
				this.getList();	
			}	
		},
		// 修改页面背景
		receptFlag(e){
			this.flag = e
		},
		// 请求数据
		getList(){
			this.$http.selectOfferResultList(this.params).then(res=>{
				if(res && res.code == 200){
					this.list = res.data;
					console.log(this.list)
					this.list.map(item=>{
						if(item.offer){
							item.offer.customerHouseArea = Math.trunc(item.offer.customerHouseArea) + 'm²';
							item.offer.finalPrice = parseInt((item.offer.finalPrice/10000)*10)/10;
							if(item.offer.finalPrice.toString().indexOf('.') == -1){
								item.offer.finalPrice = item.offer.finalPrice + '.0';
							}
						}
					})
				}else{
					uni.showToast({
						icon: "none",
						title: res.msg || Messages.FAIL_INFO
					})
				}
			})
		},
		// 跳转到搜索页面
		toSearch(item){
			/* if(item.offer !== null){
				item.offer.id = this.decIndex
				item.offer.decorateStyle = item.offer.decorateType;
			} */
			this.$openPage({
				name: 'search',
				query: {
					item
				}
			})
		},
		// 跳转到案例详情
		toDetail(quoteId){
			this.$openPage({
				name: 'example',
				query: {
					quoteId
				}
			})
		},
		// 请求户型集合
		getLayout(area){
			this.$http.getCategoryByArea({area}).then(res=>{
				this.layoutList = res.data
			})
		}
	},
	mounted(){
		// 下拉框里面的内容
		let list =  uni.getStorageSync('catalogues');
		let priceList = uni.getStorageSync('priceList');
		let layoutList = uni.getStorageSync('layoutList');
		let style= list.categorys.filter((item)=>{
			return item.type == 2;
		})
		let styleList = style.map(item=>{return{'title':item.decorateStyle, 'value':item.decorateStyle}});
		styleList.unshift({'title':'全部', 'value':'全部'})
		let decType= list.categorys.filter((item)=>{
			return item.type == 1;
		})
		let decTypeList = decType.map(item=>{return{'title':item.decorateStyle, 'value':item.decorateStyle}});
		decTypeList.unshift({'title':'全部', 'value':'全部'});
		decTypeList.pop();
		this.decTypeList = decTypeList 
		let houseTypes = layoutList.map(item=>{return{'title':item, 'value':item}});
		houseTypes.unshift({'title':'全部', 'value':'全部'})
		this.menuList[0].detailList = decTypeList;
		this.menuList[2].detailList = houseTypes;
		this.menuList[3].detailList = styleList;
		let decIndex = decTypeList.findIndex(item=>{
			return item.title == priceList[0].priceDectype
		})
		this.menuList[0].defaultSelectedIndex = decIndex;
		this.decIndex = decIndex;
		let styleIndex = styleList.findIndex(item=>{
			return item.title == priceList[3].priceManner
		})
		this.menuList[3].defaultSelectedIndex = styleIndex;
		
		let houseIndex = houseTypes.findIndex(item=>{
			return item.title == priceList[2].priceLayout
		})
		if(houseIndex !== -1){
			this.menuList[2].defaultSelectedIndex = houseIndex;
		}
		this.$refs.slFilter.resetMenuList(this.menuList);
		console.log(this.menuList)
	},
	created(){
		// 改变初始下拉框头部标题
		let priceList = uni.getStorageSync('priceList');
		this.menuList[0].title = priceList[0].priceDectype;
		this.menuList[1].title = priceList[1].priceArea + 'm²';
		if(priceList[2].priceLayout == ''){
			this.menuList[2].title = '户型'
		}else{
			this.menuList[2].title = priceList[2].priceLayout;
		}
		this.menuList[3].title = priceList[3].priceManner;
		this.params.decorateType = priceList[0].priceDectype;
		this.params.houseArea = priceList[1].priceArea;
		this.params.customerHouseType = priceList[2].priceLayout;
		this.params.style = priceList[3].priceManner;
		this.getList();
		this.$refs.slFilter.resetMenuList(this.menuList);
	},
}
